import React from "react";
import style from "./index.module.scss";

import HtmlReactParser from "html-react-parser";
import { format } from "date-fns";

import useBlog from "./useBlog";
import InlineLoader from "@components/InlineLoader";

export default function Blog() {
  const { blog, isLoading } = useBlog();

  return isLoading ? (
    <InlineLoader height="90vh" />
  ) : (
    <div className={style.wrapper}>
      <article>
        <div className={style.image}>
          <img src={blog.photo} alt="" />
        </div>
        <div className={style.blogContent}>
          <div className={style.date}>{format(new Date(blog.published_date), "MM/dd/yyyy")}</div>
          <div className={style.title}>{blog.title}</div>
          <div className={style.body}>{HtmlReactParser(blog.content)}</div>
        </div>
      </article>
    </div>
  );
}
